		width: 70px;
		height: 70px;
		top: 10px;
		right: 10px;
		background: url(../../../static/img/ws.png);
		background-size: 100%;
		z-index: 100;
	}
	#order-create .redPacket-list .redPacket-list-item .use{
		display: block;
		position: absolute;
		width: 70px;
		height: 70px;
		top: 10px;
		right: 10px;
		background: url(../../../static/img/wu.png);
		background-size: 100%;
		z-index: 100;
